<template>
  <div class="players">
    <h2 class="header">Team Maker</h2>
    <h3>Usage</h3>
    <p>
      You enter your players with their respective names and rating from 0-10 (only integers). 
    </p>
    <p>
      After that, press the mix button and get two balanced teams.
    </p>
    <p>
      If you don't like the teams that were made, go back and mix again.
    </p>
    <div v-if="this.mixed">
      <b-row>
        <b-col cols="6">
          <h6>TEAM A</h6>
          <p>Total rating {{this.sumOfRating(this.option1.team1)}}.
          </p>
          <b-table class="table1" :items="this.option1.team1">
          </b-table>
        </b-col>
        <b-col cols="6">
          <h6>TEAM B</h6>
          <p>Total rating {{this.sumOfRating(this.option1.team2)}}.
          </p>
          <b-table class="table1" :items="this.option1.team2">
          </b-table>  
        </b-col>
      </b-row>
      <b-button variant="info" @click="changeMixed()">Back</b-button>
    </div>
    <div v-else>
      <b-form-input class="input1" v-model="actualName" :state="stateName" placeholder="Enter name"></b-form-input>
      <b-form-input class="input1" type="number" v-model="actualRate" :state="stateRate" placeholder="Enter rating from 0-10 (ONLY INTEGERS)">
      </b-form-input>
      <div>
          <b-button class="button1" variant="primary" @click="addPlayer()">Add</b-button>
          <b-button class="button1" variant="warning" @click="resetEverything()">Reset</b-button>
          <b-button class="button1" variant="info" @click="mix2teams()">Mix</b-button>
      </div>
      <b-table class="table1" striped hover :items="players">
      </b-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mixed: false,
      actualName: '',
      actualRate: '',
      players: [
        {name: 'Heber', rate: 8},
        {name: 'Joan', rate: 3},
        {name: 'Carlos', rate: 5},
        {name: 'Trias', rate: 7},
        {name: 'Gesa', rate: 8},
        {name: 'Pau', rate: 6},
        {name: 'Gabi', rate: 6},
        {name: 'Tote', rate: 3},
        {name: 'Agus', rate: 7},
        {name: 'Josep', rate: 3},
      ],
      option1: {
        team1: [],
        team2: [],
      },
      option2: {
        team1: [],
        team2: []
      },
      option3: {
        team1: [],
        team2: []
      }
    }
  },
  computed: {
    stateName(){
      return this.actualName != "" && this.actualName.length < 15;
    },
    stateRate(){
      return this.actualRate >= 0 && this.actualRate <=10 && this.actualRate != "";
    }
  },
  methods: {
    resetEverything: function(){
      this.resetInputs();
      this.players = [];
    },
    resetInputs: function(){
      this.actualName = '';
      this.actualRate = '';
    },
    addPlayer: function(){
      var player = {};
      if(this.stateRate && this.stateName){
        player.name = this.actualName;
        player.rate = parseInt(this.actualRate);
        this.players.push(player);
        this.resetInputs();
      }
    },
    sumOfRating: function(team){
      var result = 0;
      for(var i = 0; i < team.length; ++i){
        result = result + team[i].rate;
      }
      return result;
    },
    changeMixed: function(){
      this.mixed = !this.mixed;
    },
    mix2teams: function(){
      this.changeMixed();
      var aux = this.players.slice();
      var index = 0;
      var limitIt = 300;
      while(aux.length != 0 && limitIt != 0){
        index = Math.floor(Math.random() * aux.length);
        if(this.option1.team1.length < this.players.length/2){
          this.option1.team1.push(aux.splice(index, 1)[0]);
        }
        else{
          this.option1.team2.push(aux.splice(index, 1)[0]);
        }
        if(aux.length == 0){
          limitIt = limitIt - 1;
          if(this.sumOfRating(this.option1.team1) != this.sumOfRating(this.option1.team2)){
            aux = this.players.slice();
            this.option1.team1 = [];
            this.option1.team2 = [];
          }
        }
      }
    }
  }
}
</script>
<style scoped>
.header{
  color: #343434;
  font-size: 22px;
  line-height: 40px;
  font-weight: normal;
  text-transform: uppercase;
  font-family: 'Orienta', sans-serif;
  letter-spacing: 1px;
  font-style: italic;
  background-color: cadetblue;
}

.input1{
  max-width: 400px;
  display: block;
  margin: 5px auto;
}

.button1{
  margin: 10px 5px;
}

.table1{
  margin: 0px auto 10px;
  max-width: 500px;
}
</style>
